const initialState = {
    isAuthorized: false,
    name: "",
}

const mainReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'AUTHORIZED':
            return Object.assign({},{ isAuthorized: true , name:action.payload.name})
        default:
            return state;
    }
}

export default mainReducer;