import {connect} from 'react-redux';

import Menu from '../components/Menu'
import {onGetAuth} from '../actionCreators/actions'

export default connect(
    state => ({
        isAuthorized: state.mainReducer.isAuthorized,
        name: state.mainReducer.name
    }),
    dispatch => ({
        onGetAuth: () => dispatch(onGetAuth())
    })
)(Menu)