import React, { PureComponent } from 'react';
import './Register.css';
import { Link } from 'react-router-dom';


class Main extends PureComponent {

    componentDidMount() {
        document.querySelector('.form').addEventListener("submit", (e)=>{
            console.log(e);
        });
    }
    render() {
        return (
            <React.Fragment>
                <div className="form">
                    <form method="post" action="http://localhost:3030//login" >
                        <div className="mail"><img src="../images/mail.svg" alt="mail" /><input type="email" name="mail" required placeholder="Электронная почта" /></div>
                        <div className="pass"><img src="../images/lock.svg" alt="password" /><input type="password" name="password" required placeholder="Пароль" /></div>
                        <div className="btn">
                            <button type="submit">Вход</button>
                        </div>
                    </form>
                    <div className="social-login">
                        <a href="http://localhost:3030/users/github" className="git">
                            <i className="fab fa-github"></i>
                        </a>
                        <a href="http://localhost:3030/users/github" className="google">
                            <i className="fab fa-google"></i>
                        </a>
                    </div>
                    <p>Нет учётной записи?
                        <Link to="/register">Создать учётную запись</Link>
                    </p>
                </div>
            </React.Fragment >
        );
    }
}

export default Main;
