import React, { PureComponent } from 'react';

import './SupportCompany.css';


class SupportCompany extends PureComponent {
    render() {
        return (
            <section id="partners-list">
                <div className="wrapper-list">
                    <h3>
                        WE ARE WORKING WITH
                    </h3>
                    <div className="company">
                        <a href="/">
                            <img src="./img/freelanser.png" alt="freelanser" />
                        </a>
                        <p>
                            Freelancer.com is the world's largest freelancing and crowdsourcing marketplace by number of users and projects.
                             We connect over 28,392,944 employers and freelancers globally from over 247 countries, regions and territories.
                              Through our marketplace, employers can hire freelancers to do work in areas such as software development,
                               writing, data entry and design right through to engineering, the sciences, sales and marketing, accounting and legal services.
                               Freelancer Limited is trading on the Australian Securities Exchange under the ticker ASX:FLN
                        </p>
                    </div>
                    <div className="company">
                        <a href="/">
                            <img src="./img/upwork.png" alt="freelanser" />
                        </a>
                        <p>
                        Upwork is the world's largest freelancing website.
                         Upwork makes it easier for the world's best businesses and independent professionals to find each other without the traditional barriers of set time and place.
                          Freelancers on the site offer more than 3,500 skills, helping to ease the pain businesses face in finding skills they need to get work done.
                           These freelancers are earning more than $1 billion annually via Upwork. Upwork makes it simple, 
                           quick and cost-effective for clients to find, hire, work with and pay freelancers.
                         Businesses of every size use Upwork, from one-person startups to major corporations.
                        </p>
                    </div>
                </div>
            </section>
        )
    }
}

export default SupportCompany;
