import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';

import soc from '../../assets/soc';

import './TouchWithUs.css';
import Social from '../Social';


class TouchWithUs extends PureComponent {
    render() {
        return (
            <footer id="contacts-list">
                <div className="contact-title">
                    <div className="wrap">
                        <h5>We would love to know you</h5>
                        <h3>GET IN TOUCH WITH US</h3>
                        <form action="" method="post">
                            <div className="row">
                                <div className="p-name">
                                    <input type="text" name="your-name" placeholder="Name*" />
                                </div>
                                <div className="p-email">
                                    <input type="email" name="your-email" placeholder="Emal*" />
                                </div>
                                <div className="p-subject">
                                    <input type="text" name="your-subject" placeholder="Subject*" />
                                </div>
                            </div>
                            <div className="p-message">
                                <textarea name="your-massege" rows="10" cols="40" placeholder="Message"></textarea>
                            </div>
                            <input type="submit" value="Send on email" className="sub-mail" />
                        </form>
                    </div>
                    <div className="footer-soc">
                        <div className="logo-header">
                            <Link to='/'>
                                <img src='./img/logo_light.png' alt='logo' />
                            </Link>
                        </div>
                        <Social hrefs={soc} />
                        <p>Hazel © Punko Sergey 2018</p>
                    </div>
                </div>
            </footer >
        )
    }
}

export default TouchWithUs;
