import React, { Component } from 'react';
import './ChooseJob.css';
import Slider from 'react-slick';

let show = 3;

class ChooseJob extends Component {
    constructor() {
        super();
        this.state = {
            width: window.innerWidth
        };
        this.updateDimensions = this.updateDimensions.bind(this)
    }
    componentDidMount() {
        window.addEventListener("resize", this.updateDimensions);
    }

    updateDimensions() {
        this.setState({ width: window.innerWidth });
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.updateDimensions);
    }

    render() {
        if (this.state.width > 900) {
            show = 3;
        } else if (600 < this.state.width && this.state.width < 900) {
            show = 2;
        } else {
            show = 1;
        }
        const settings = {
            infinite: true,
            speed: 1000,
            slidesToShow: show,
            slidesToScroll: 1,
            autoplay: true
        };
        return (
            <section className="choose" id="projects-list">
                <div className="choose-project">
                    <h4 className="question">
                        WHAT YOU CHOOSE?
                    </h4>
                    <Slider {...settings}>
                        <div className="projects item">
                            <a href="/filters">
                                <img src="./img/analytics.png" className="size-img" alt="analytics" />
                                <p className="description">Analytics</p>
                            </a>
                        </div>
                        <div className="projects item">
                            <a href="/filters">
                                <img src="./img/coding.png" className="size-img" alt="coding" />
                                <p className="description">
                                    Web-developing
                                </p>
                            </a>
                        </div>
                        <div className="projects  item">
                            <a href="/filters">
                                <img src="./img/domain-registration.png" className="size-img" alt="" />
                                <p className="description">Design</p>
                            </a>
                        </div>
                        <div className="projects item">
                            <a href="/filters">
                                <img src="./img/shield.png" className="size-img" alt="" />
                                <p className="description">Security</p>
                            </a>
                        </div>
                        <div className="projects item">
                            <a href="/filters">
                                <img src="./img/server.png" className="size-img" alt="" />
                                <p className="description">Web-Application</p>
                            </a>
                        </div>
                        <div className="projects item">
                            <a href="/filters">
                                <img src="./img/tags.png" className="size-img" alt="" />
                                <p className="description">Seo</p>
                            </a>
                        </div>
                        <div className="projects item">
                            <a href="/filters">
                                <img src="./img/stats.png" className="size-img" alt="" />
                                <p className="description">Statistics</p>
                            </a>
                        </div>
                    </Slider>
                </div >
            </section>
        )
    }
}

export default ChooseJob;
