import React, { PureComponent } from 'react';
import './Register.css';
import { Link } from 'react-router-dom';


class Main extends PureComponent {

    render() {
        return (
            <React.Fragment>
                <div className="form">
                    <form method="post" action="http://localhost:3030/register" >
                        <div className="name">
                            <img src="../images/employee.svg" alt="user" />
                            <input type="text" name="username" required placeholder="Имя" />
                        </div>
                        <div className="mail">
                            <img src="../images/mail.svg" alt="mail" />
                            <input type="email" name="mail" required placeholder="Электронная почта" />
                        </div>
                        <div className="pass">
                            <img src="../images/lock.svg" alt="password" />
                            <input type="password" name="password" required placeholder="Пароль" />
                        </div>
                        <div className="btn">
                            <button type="submit">Создать бесплатную учётную запись</button>
                        </div>
                    </form>
                    <div className="social-login">
                        <a href="http://localhost:3030/users/github" className="git">
                            <i className="fab fa-github"></i>
                        </a>
                        <a href="http://localhost:3030/users/github" className="google">
                            <i className="fab fa-google"></i>
                        </a>
                    </div>
                    <p>Уже зарегестрированы?
                        <Link to="/login">Вход</Link>
                    </p>
                </div>
            </React.Fragment >
        );
    }
}

export default Main;
