import axios from 'axios'

export const onAuth = (username) => {
    return {
        type: 'AUTHORIZED',
        payload: {
            name: username,
        }
    }
}





export const onGetAuth = () => async dispatch => {
    const info = await axios.get('/uservalid').then(res => res.data).catch((err) => { console.log(err) });
    dispatch(onAuth(info.isAuth))
};



