import React, { PureComponent } from 'react';
import './Menu.css';
import Social from '../Social';
import ChooseLang from './Choose_lang';
import navigationHref from '../../assets/navigationHref';
import soc from '../../assets/soc';
import Navigation from '../Navigation';
import { Link } from 'react-router-dom';

class Menu extends PureComponent {
    componentDidMount() {
        this.props.onGetAuth();
    }

    render() {
        const {isAuthorized, name} = this.props;

        console.log(isAuthorized)
        return (
            <div className="wrapper">
                <nav className="navbar-container">
                    <div className="logo-header">
                        <Link to='/'>
                            <img src='./img/logo_light.png' alt='logo' />
                        </Link>
                    </div>
                    <div className="right-social-menu">
                        <Navigation items={navigationHref} />
                        <div className="userLogin">
                            {
                                isAuthorized? 
                                <div className="profile"><h1 className='username'>{name}<a href="http://localhost:3030/logout">Logout</a></h1></div> : 
                                <Link to='/login' className="login">Login</Link>
                            }
                        </div>
                        <ChooseLang />
                        <Social hrefs={soc} />
                    </div>
                </nav>
            </div>
        )
    }
}

export default Menu;
