import React, { PureComponent } from 'react';
import './About.css';



class About extends PureComponent {

    render() {
        return (
            <section id="about-list">
                <div className="title-about">
                    <div className="vertical-line">
                        <span className="bord"></span>
                    </div>
                    <div className="about-us">
                        <h6>WE ARE HAZEL</h6>
                        <h3>ABOUT US</h3>
                        <p className="about">One of the most popular problem for all freelancers is how find customer.
                         Our project Hazel combine vacancy from different platforms and can help you choose job.
                         You can find vacancy whatever your level. 
                         We listen to your suggestions for improving the site and try to do better. Join us!</p>
                    </div>
                    <div className="vertical-line">
                        <span className="bord"></span>
                    </div>
                </div>
            </section>
        );
    }
}

export default About;
