import React, { PureComponent } from 'react';
import './Choose_lang.css';

class Choose_lang extends PureComponent {
    render() {
        return (
            <ul className="language-buttom">
                <li className="auth">
                    <a href="/en" className="language">eng</a>
                </li>
                <li className="auth">
                    <a href="/ru" className="language">rus</a>
                </li>
            </ul>
        )
    }
}

export default Choose_lang;
